body {
    font-family: sans-serif;
    padding: 1em;
  }
  
  .ais-ClearRefinements {
    margin: 1em 0;
  }
  
  .ais-SearchBox {
    margin: 1em 0;
  }
  
  .ais-Pagination {
    margin-top: 1em;
  }
  
  .left-panel {
    float: left;
    width: 200px;
  }
  
  .right-panel {
    margin-left: 210px;
  }
  
  .ais-InstantSearch {
    max-width: 960px;
    overflow: hidden;
    margin: 0 auto;
  }
  
  .ais-Hits-item {
    margin-bottom: 1em;
    width: calc(50% - 1rem);
  }
  
  .ais-Hits-item img {
    margin-right: 1em;
    width: 100%;
    height: 100%;
    margin-bottom: 0.5em;
  }
  
  .hit-name {
    margin-bottom: 0.5em;
  }
  
  .hit-info {
    font-size: 90%;
  }
  
  .ais-InfiniteHits-item {
    margin-bottom: 1em;
    width: calc(50% - 1rem);
  }
  